import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { OnlineCoachingPage } from './online-coaching.page';

const routes: Routes = [
  {
    path: '',
    component: OnlineCoachingPage
  },
  {
    path: 'course-usmle',
    loadChildren: () => import('./course-usmle/course-usmle.module').then( m => m.CourseUsmlePageModule)
  },
  {
    path: 'course-neet-crash',
    loadChildren: () => import('./course-neet-crash/course-neet-crash.module').then( m => m.CourseNEETCrashPageModule)
  },
  {
    path: 'course-nmat',
    loadChildren: () => import('./course-nmat/course-nmat.module').then( m => m.CourseNmatPageModule)
  },
  {
    path: 'course-fsp',
    loadChildren: () => import('./course-fsp/course-fsp.module').then( m => m.CourseFspPageModule)
  },
  {
    path: 'course-next',
    loadChildren: () => import('./course-next/course-next.module').then( m => m.CourseNextPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class OnlineCoachingPageRoutingModule {}
